<script>
  import { createEventDispatcher } from 'svelte'
  import collapse from 'svelte-collapse'

  export let data;

  export let open = !!(data.rows && data.rows.length < 5);
  export let duration = 0.2;
  export let easing = 'ease-in';
  const dispatch = createEventDispatcher();

  function handleToggle () {
      open = !open
      if (open) {
          dispatch('open');
      }
      else {
          dispatch('close');
      }
  }
</script>

<div class="table">
  <div class="header">
    <span class="title">{data.title}</span>
    {#if data.note}
      <span class="note">{data.note}</span>
    {/if}
    <span class="chevron" on:click={handleToggle}>
      <img src="chevron.png" alt="Show/Hide"width="16" height="16"/> 
    </span>
  </div>

  <div class="table-body" use:collapse={{open, duration, easing}}>
    {#if data.text}
      <div class="raw-data">{@html data.text}</div>
    {/if}

    {#if data.rows && data.rows.length}
      {#each data.rows as row}
        <div class="row">
          <div class="name">{row.name}</div>
          <div class="value">{@html row.value}</div>
        </div>
        {#if row.description}
        <div class="row description">
          {@html row.description}
        </div>
        {/if}
      {/each}
    {/if}
  </div>
</div>

<style>
  .table {
    padding: 10px 0;
    border-bottom: 1px solid #cdcdcd;
    margin-bottom: 20px;
  }

  .table .header {
    font-size: 18px;
    font-weight: 600;
    margin-bottom: 10px;
  }

  .table .header span, .table .header .note {
    display: inline-block;
    vertical-align: middle;
  }

  .table .header .note {
    font-size: 12px;
    margin-left: 5px;
    padding: 3px 5px;
    background: #ababab;
    border-radius: 3px;
    color: #fff;
  }

  .chevron {
    float: right;
    padding: 3px 5px;
  }

  .chevron:hover {
    cursor: pointer;
  }

  .table-body .row {
    display: flex;
    justify-content: space-between;
    font-size: 13px;
  }

  .table-body .row .name, .table-body .row .value {
    padding: 10px 0;
    overflow-wrap: break-word;
  }

  .table-body .row .name {
    font-weight: 600;
    min-width: 35%;
  }

  .table-body .row .value {
    width: 65%;
  }

  .value {
    color: magenta
  }

  .table-body .row.description {
    color: #787878;
    margin-bottom: 10px;
  }
</style>
